.profile {
    margin-top: 95px;

    &__img {
        border-radius: 50%;
        padding: 10px;
        border: 1px solid #f5f5f5;
        display: block;
        margin: 0 auto 40px;
        width: 100px;
        height: 100px;
    }

    .form-control {
        background-color: #f5f5f5;
        border: none;
        color: $black;
        box-shadow: 0 0.4rem #dfd9d9;
    }

    .form-label {
        color: $black;
    }
}